import React from "react";
import Accordion from "components/atom/Accordion";
import BasicTable from "components/atom/CustomBasicTable";
import ProfileDescription from "components/atom/ProfileDescription";
import ProfileDetails from "components/atom/ProfileDetails";
import {
  PHARMACY_USER_VIEW,
  STATUS_OPTIONS,
  STATUS_UPDATE,
} from "shared/constants";
import CustomTable from "components/atom/table";
import { experienceToYears, getLocalizedDateString } from "components/utility";
import { URL_HEAD } from "shared/api";
import { Images } from "shared/images";
import { Fade, Tooltip } from "@material-ui/core";

const tableJobHeader = [
  "#",
  "Name",
  "Position",
  "Start Date",
  "End Date",
  "Description",
];

const tableEducationHeader = [
  "#",
  "Name",
  "Qualification",
  "Start Date",
  "End Date",
  "Description",
];

const tableHeaderFunc = [
  "#",
  "Document Name",
  "Uploaded Document",
  "Status",
  "Action",
];

function Profile({ data, onUnverifyClick, onVerifyClick, onDocumentView }) {
  const data2 = [
    {
      title: "Name",
      value: data?.first_name + " " + data?.last_name,
      isVerified: parseInt(data?.verified_document) == 100,
    },
    {
      title: "Phone",
      value: data?.personal_info?.phone_number
        ? data?.personal_info?.phone_number
        : "N.A",
    },
    {
      title: "DOB",
      value: data?.personal_info?.dob
        ? getLocalizedDateString(data?.personal_info?.dob)
        : "N.A",
    },
    {
      title: "Email Address",
      value: data?.email ? data?.email : "N.A",
      isVerified: data?.personal_info?.verified,
    },
    {
      title: "Address",
      value: data?.address_info ? data?.address_info : "N.A",
    },
    {
      title: "Registration Number",
      value: data?.personal_info?.reg_number
        ? data?.personal_info?.reg_number
        : "N.A",
    },
    {
      title: "Insurance Valid Till",
      value: data?.valid_till
        ? getLocalizedDateString(data?.valid_till)
        : "N.A",
    },
    {
      title: "Total Experience",
      value:
        experienceToYears(data?.experience?.years, data?.experience?.months) +
        " Years",
    },
  ];

  const ButtonVerifiedUnverified = ({ isverified, documentType, id, docs }) => {
    console.log(isverified, documentType, id, docs);
    debugger;
    if (isverified === STATUS_OPTIONS.UNVERIFIED.key) {
      return (
        <button
          className="btn btn-sm btn-danger m-1"
          onClick={() => {
            onUnverifyClick(id, documentType, docs);
          }}
        >
          {STATUS_OPTIONS.UNVERIFY.title}
        </button>
      );
    } else {
      return (
        <button
          className="btn btn-sm btn-secondary m-1"
          onClick={() => onVerifyClick(documentType)}
        >
          {STATUS_OPTIONS.VERIFY.title}
        </button>
      );
    }
  };

  return (
    <div className="tab_content">
      <ProfileDetails
        data={data2}
        imageURL={
          data?.personal_info?.photo
            ? URL_HEAD + data?.personal_info?.photo
            : Images.defaultUserPic
        }
        rating={3}
      />
      {data?.personal_info?.personal_description ? (
        <ProfileDescription
          title={"Description"}
          description={
            data?.personal_info?.personal_description
              ? data?.personal_info?.personal_description
              : "N.A"
          }
        />
      ) : null}

      <section className="accordin_sec">
        <Accordion title={"Education"}>
          {data?.edu_history?.length ? (
            <div className="customTable">
              <CustomTable>
                <thead>
                  <tr key={1000 + 1}>
                    {tableEducationHeader.map((i, idx) => (
                      <th scope="col" id={idx}>
                        {i}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data?.edu_history?.length ? (
                    data?.edu_history?.map((i, idx) => {
                      return (
                        <>
                          <tr>
                            <td>{idx + 1} </td>
                            <td>{i?.institution_name} </td>
                            <td>{i?.qualification} </td>
                            <td>{i?.start_date} </td>
                            <td>{i?.end_date} </td>
                            <td>{i?.description} </td>
                          </tr>
                        </>
                      );
                    })
                  ) : (
                    <tr>
                      <td colSpan={6} className="text-center py-5">
                        No Education Details Uploaded
                      </td>
                    </tr>
                  )}
                </tbody>
              </CustomTable>
              {/* <BasicTable
                    theadData={tableHeader}
                    tbodyData={
                      data?.edu_history?.length ? data?.edu_history : []
                    }
                  /> */}
            </div>
          ) : (
            <span className="no_result py-3">
              {"No Education Details Uploaded"}{" "}
            </span>
          )}
        </Accordion>
        <Accordion title={"Experience"}>
          {data?.job_history?.length ? (
            <div className="customTable">
              <CustomTable>
                <thead>
                  <tr key={1000 + 1}>
                    {tableJobHeader.map((i, idx) => (
                      <th scope="col" id={idx}>
                        {i}
                      </th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {data?.job_history?.length ? (
                    data?.job_history?.map((i, idx) => {
                      return (
                        <>
                          <tr>
                            <td>{idx + 1} </td>
                            <td>{i?.company_name} </td>
                            <td>{i?.job_title} </td>
                            <td>{i?.start_date} </td>
                            <td>{i?.end_date} </td>
                            <td>{i?.description} </td>
                          </tr>
                        </>
                      );
                    })
                  ) : (
                    <tr>
                      <td colSpan={6} className="text-center py-5">
                        No Experience Details Uploaded
                      </td>
                    </tr>
                  )}
                </tbody>
              </CustomTable>
            </div>
          ) : (
            <span className="no_result py-3">
              {"No Experience Details Uploaded"}{" "}
            </span>
          )}
        </Accordion>
      </section>

      <div className="card">
        <div className="card-body">
          <h2 className="h6 text-captialize">Uploaded Documents</h2>

          {data?.documents?.length ? (
            <CustomTable>
              <thead>
                <tr key={1000 + 1}>
                  {tableHeaderFunc.map((i, idx) => (
                    <th scope="col" id={idx}>
                      {i}
                    </th>
                  ))}
                </tr>
              </thead>

              {data?.documents?.length ? (
                data?.documents?.map((i, idx) => {
                  const documentType = i.document_type;
                  const title =
                    PHARMACY_USER_VIEW.DOCUMENT_TYPE_DATA[documentType]?.title;
                  return (
                    <>
                      <tbody>
                        <tr>
                          <td>{idx + 1} </td>
                          <td>{title ? title : "N.A"} </td>
                          <td
                            onClick={() => {
                              onDocumentView(
                                i?.id,
                                documentType,
                                data?.documents
                              );
                            }}
                          >
                            <span className="badge btn-sm badge-grey">
                              {i?.document ? i?.name : "Not Uploaded"}
                            </span>
                          </td>
                          <td>
                            <div className="d-flex align-items-center">
                              <span
                                className={`m-1 badge-md badge-${
                                  i?.verified === STATUS_OPTIONS.PENDING.key
                                    ? "info"
                                    : i?.verified ===
                                      STATUS_OPTIONS.VERIFIED.key
                                    ? "success"
                                    : i?.verified ===
                                      STATUS_OPTIONS.UNVERIFIED.key
                                    ? "danger"
                                    : "grey"
                                }`}
                              >
                                {STATUS_UPDATE[i?.verified]}
                                {/* {i?.verified === STATUS_OPTIONS.PENDING.key
                                  ? STATUS_UPDATE[i?.verified]
                                  : i?.verified === STATUS_OPTIONS.VERIFIED.key
                                  ? STATUS_UPDATE[i?.verified]
                                  : i?.verified ===
                                    STATUS_OPTIONS.UNVERIFIED.key
                                  ? STATUS_UPDATE[i?.verified]
                                  : STATUS_UPDATE[STATUS_OPTIONS.NOT_UPLOADED.key]} */}
                              </span>

                              {i?.verified === STATUS_OPTIONS.UNVERIFIED.key ? (
                                <span className="position-relative">
                                  <Tooltip
                                    title={i?.comment ? i?.comment : "N.A"}
                                    arrow={true}
                                    placement={"bottom"}
                                    enterDelay={200}
                                    TransitionComponent={Fade}
                                    TransitionProps={{ timeout: 300 }}
                                  >
                                    <em className="ms-2 ver_icon">
                                      <img
                                        src={Images.infoIcon}
                                        alt="info"
                                        width={20}
                                      />
                                    </em>
                                  </Tooltip>
                                </span>
                              ) : null}
                            </div>
                          </td>
                          <td width={230}>
                            <div className="d-flex">
                              {i?.verified === STATUS_OPTIONS.VERIFY.key ? (
                                <button
                                  className="btn btn-sm btn-danger m-1"
                                  onClick={() => {
                                    onUnverifyClick(
                                      i?.id,
                                      documentType,
                                      data?.documents
                                    );
                                  }}
                                  disabled={
                                    i?.verified ===
                                    STATUS_OPTIONS.UNVERIFIED.key
                                  }
                                >
                                  {STATUS_OPTIONS.UNVERIFY.title}
                                </button>
                              ) : i?.verified ===
                                STATUS_OPTIONS.UNVERIFIED.key ? (
                                <button
                                  className="btn btn-sm btn-secondary m-1"
                                  onClick={() => onVerifyClick(documentType)}
                                  disabled={
                                    i?.verified === STATUS_OPTIONS.VERIFY.key
                                  }
                                >
                                  {STATUS_OPTIONS.VERIFIED.title}
                                </button>
                              ) : (
                                <>
                                  <button
                                    className="btn btn-sm btn-secondary m-1"
                                    onClick={() => onVerifyClick(documentType)}
                                    disabled={
                                      i?.verified === STATUS_OPTIONS.VERIFY.key
                                    }
                                  >
                                    {STATUS_OPTIONS.VERIFIED.title}
                                  </button>
                                  <button
                                    className="btn btn-sm btn-danger m-1"
                                    onClick={() => {
                                      onUnverifyClick(
                                        i?.id,
                                        documentType,
                                        data?.documents
                                      );
                                    }}
                                    disabled={
                                      i?.verified ===
                                      STATUS_OPTIONS.UNVERIFIED.key
                                    }
                                  >
                                    {STATUS_OPTIONS.UNVERIFY.title}
                                  </button>
                                </>
                              )}
                            </div>
                          </td>
                        </tr>
                      </tbody>
                    </>
                  );
                })
              ) : (
                <tbody>
                  <tr>
                    <td colSpan={5} className="text-center py-5">
                      No Documents Uploaded
                    </td>
                  </tr>
                </tbody>
              )}
            </CustomTable>
          ) : (
            <span className="no_result py-3">{"No Documents Uploaded"} </span>
          )}
        </div>
      </div>
    </div>
  );
}

export default Profile;
